/*
** Vendor - Flickity
** ----------------------------------------------------------------------------- */
/* prevent page scrolling when flickity is fullscreen */
.is-flickity-fullscreen {
  overflow: hidden;
}

.flickity-enabled {
  position: relative;
}
.flickity-enabled:focus {
  outline: none;
}
.flickity-enabled.is-draggable {
  user-select: none;
}
.flickity-enabled.is-draggable .flickity-viewport {
  cursor: grab;
}
.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: grabbing;
}
.flickity-enabled.is-fade .flickity-slider > * {
  pointer-events: none;
}
.flickity-enabled.is-fade .flickity-slider > .is-selected {
  pointer-events: auto;
}

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
  transition: height 0.25s;
}
.is-fullscreen .flickity-viewport {
  transition: none;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

.flickity-button {
  display: block;
  position: absolute;
  bottom: 100%;
  z-index: var(--chipmunk--layer--utils);
  width: 1em;
  height: 1em;
  margin-bottom: calc(var(--chipmunk--layout--column-gutter) * 0.5);
  font-size: 1.25em;
  transition-property: font-size, opacity;
  transition-duration: var(--chipmunk--transition-duration);
}
.flickity-button.previous {
  right: calc(var(--chipmunk--layout--column-gutter) * 0.5 + 1em + calc(var(--chipmunk--spacer) * 0.5));
}
.flickity-button.next {
  right: calc(var(--chipmunk--layout--column-gutter) * 0.5);
}
.flickity-button svg {
  display: block;
  height: 1em;
  width: 1em;
  fill: currentColor;
  stroke: none;
}
.flickity-button:not([disabled]):hover {
  cursor: pointer;
  opacity: 0.75;
}
.flickity-button[disabled] {
  opacity: 0.5;
  pointer-events: none;
}

