  }
}
.c-menu-mobile > .menu-item > a[href] {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
.c-menu-mobile > .menu-item > a[href]:hover {
  color: var(--chipmunk--color--black);
}
.c-menu-mobile > .menu-item.current-menu-item > a, .c-menu-mobile > .menu-item.current-menu-ancestor > a, .c-menu-mobile > .menu-item.current-page-ancestor > a {
  color: var(--chipmunk--color--black);
}
.c-menu-mobile .menu-item {
  position: relative;
}
.c-menu-mobile .menu-item a {
  display: block;
  padding-top: 0.25em;
  padding-bottom: 0.25em;
}
.c-menu-mobile .menu-item.menu-item-has-children a {
  padding-right: calc(calc(var(--chipmunk--typography--line-height) * 1 * 1em) + 1em - 0.75em);
}
.c-menu-mobile .menu-toggle {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(calc(var(--chipmunk--typography--line-height) * 1 * 1em) + 1em);
  width: calc(calc(var(--chipmunk--typography--line-height) * 1 * 1em) + 1em);
  margin-right: -0.75em;
  opacity: 0.5;
  transition: transform var(--chipmunk--transition-duration);
}
.c-menu-mobile .sub-menu {
  display: none;
  border-left: 1px solid var(--chipmunk--color--gray-lighter);
}
.c-menu-mobile .sub-menu .menu-item {
  margin-left: 1em;
}
.c-menu-mobile .sub-menu .menu-item > a {
  position: relative;
  display: block;
}
.c-menu-mobile .sub-menu .menu-item > a:hover {
  opacity: 0.8;
}
.c-menu-mobile .sub-menu .menu-toggle {
  height: calc(calc(var(--chipmunk--typography--line-height) * 1 * 1em) + 0.5em);
  width: calc(calc(var(--chipmunk--typography--line-height) * 1 * 1em) + 0.5em);
  margin-right: -0.5em;
}
.c-menu-mobile .is-expanded > .menu-toggle {
  transform: rotate(180deg);
}
.c-menu-mobile .is-expanded > .sub-menu {
  display: block;
}

/*
** Component - Menu primary
** ----------------------------------------------------------------------------- */
.c-menu-primary .menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 0.25em clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
}
.c-menu-primary .menu > .menu-item > a {
  display: block;
}
.c-menu-primary .menu > .menu-item > a:hover {
  opacity: 0.8;
}
.c-menu-primary .menu > .menu-item.current-menu-item > a, .c-menu-primary .menu > .menu-item.current-menu-ancestor > a, .c-menu-primary .menu > .menu-item.current-page-ancestor > a {
  color: var(--chipmunk--color--black);
}
.c-menu-primary .menu-item.menu-item-has-children {
  position: relative;
}
.c-menu-primary .menu-item.menu-item-has-children > a::after {
  content: "";
  display: inline-block;
  height: 0.35em;
  width: 0.35em;
  margin-left: 0.5em;
  border-style: solid;
  border-width: 0.075em 0.075em 0 0;
  vertical-align: 0.25em;
  opacity: 0.5;
  transform: rotate(135deg);
}
.c-menu-primary .sub-menu {
  min-width: 17.5rem;
  max-width: 20rem;
}
.c-menu-primary .sub-menu .menu-item > a::after {
  position: absolute;
  top: 50%;
  right: 1em;
  margin-left: 0;
  margin-top: -0.25em;
  vertical-align: initial;
  transform: rotate(45deg);
}
.c-menu-primary .sub-menu .menu-item.menu-item-has-children > a {
  padding-right: 2em;
}
.c-menu-primary .sub-menu.theme-light {
  position: absolute;
  top: calc(100% + 1.5rem);
  z-index: var(--chipmunk--layer--dropdown);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  background-color: var(--chipmunk--color--section);
  border-radius: var(--chipmunk--border-radius);
  border: 1px solid var(--chipmunk--color--gray-lighter);
  color: var(--chipmunk--color--black);
  font-size: 1.4rem;
  opacity: 0;
  pointer-events: none;
  transform: translate3d(0, 0.75rem, 0);
  transition-property: opacity, transform;
  transition-duration: var(--chipmunk--transition-duration);
  left: 0;
  text-align: left;
}
.c-menu-primary .sub-menu.theme-light::before, .c-menu-primary .sub-menu.theme-light::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 100%;
}
.c-menu-primary .sub-menu.theme-light::before {
  height: 1.5rem;
  left: 0;
  right: 0;
}
.c-menu-primary .sub-menu.theme-light::after {
  border: 0.75rem solid transparent;
  border-bottom-color: var(--chipmunk--color--section);
  filter: drop-shadow(0 -1px 0 var(--chipmunk--color--gray-lighter));
  left: 1.5rem;
}
.c-menu-primary .sub-menu.theme-light ul {
  top: calc(0.5em - 1.5rem);
  right: auto;
  left: calc(100% + 1.5rem);
}
.c-menu-primary .sub-menu.theme-light ul::before {
  height: auto;
  width: 1.5rem;
  top: 0;
  bottom: 0;
  right: 100%;
  left: auto;
}
.c-menu-primary .sub-menu.theme-light ul::after {
  border-bottom-color: transparent;
  bottom: auto;
  top: 1.5rem;
  left: -1.5rem;
  border-right-color: var(--chipmunk--color--section);
  filter: drop-shadow(-1px 0 0 var(--chipmunk--color--gray-lighter));
}
.c-menu-primary .sub-menu.theme-dark {
  position: absolute;
  top: calc(100% + 1.5rem);
  z-index: var(--chipmunk--layer--dropdown);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  background-color: var(--chipmunk--color--black);
  border-radius: var(--chipmunk--border-radius);
  border: 1px solid transparent;
  color: var(--chipmunk--color--section);
  font-size: 1.4rem;
  opacity: 0;
  pointer-events: none;
  transform: translate3d(0, 0.75rem, 0);
  transition-property: opacity, transform;
  transition-duration: var(--chipmunk--transition-duration);
  left: 0;
  text-align: left;
}
.c-menu-primary .sub-menu.theme-dark::before, .c-menu-primary .sub-menu.theme-dark::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 100%;
}
.c-menu-primary .sub-menu.theme-dark::before {
  height: 1.5rem;
  left: 0;
  right: 0;
}
.c-menu-primary .sub-menu.theme-dark::after {
  border: 0.75rem solid transparent;
  border-bottom-color: var(--chipmunk--color--black);
  filter: drop-shadow(0 -1px 0 transparent);
  left: 1.5rem;
}
.c-menu-primary .sub-menu.theme-dark ul {
  top: calc(0.5em - 1.5rem);
  right: auto;
  left: calc(100% + 1.5rem);
}
.c-menu-primary .sub-menu.theme-dark ul::before {
  height: auto;
  width: 1.5rem;
  top: 0;
  bottom: 0;
  right: 100%;
  left: auto;
}
.c-menu-primary .sub-menu.theme-dark ul::after {
  border-bottom-color: transparent;
  bottom: auto;
  top: 1.5rem;
  left: -1.5rem;
  border-right-color: var(--chipmunk--color--black);
  filter: drop-shadow(-1px 0 0 transparent);
}
.c-menu-primary .menu-item-has-children:hover > .sub-menu,
.c-menu-primary .menu-item-has-children:focus > .sub-menu {
  opacity: 1;
  pointer-events: all;
  transform: translate3d(0, 0, 0);
}

/*
** Component - Menu secondary
** ----------------------------------------------------------------------------- */
.c-menu-secondary {
  display: inline-flex;
  flex-direction: column;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  );
}

.c-menu-secondary__item {
  position: relative;
  line-height: 1.4;
}

.c-menu-secondary__link {
  display: inline-block;
  color: var(--chipmunk--color--gray);
}
.c-menu-secondary__link[href]:hover, .c-menu-secondary__link[title]:hover, .c-menu-secondary__link[type]:hover {
  color: var(--chipmunk--color--black);
}

/*
** Component - Menu socials
** ----------------------------------------------------------------------------- */
.c-menu-socials {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  );
}

.c-menu-socials__title:not(:last-child) {
  margin-right: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  );
}

.c-menu-socials__list {
  display: contents;
}

.c-menu-socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2em;
  width: 2em;
  border: var(--chipmunk--button--border-width) solid var(--chipmunk--color--gray-light);
  border-radius: var(--chipmunk--button--border-radius);
  color: var(--chipmunk--color--gray-dark);
  font-size: var(--chipmunk--button--font-size);
  line-height: 1;
  text-align: center;
}
.c-menu-socials__link:hover {
  border-color: var(--chipmunk--color--primary);
  color: var(--chipmunk--color--primary);
  opacity: 0.75;
  transform: scale(1.05);
}

/*
** Component - Menu toolbox
** ----------------------------------------------------------------------------- */
.c-menu-toolbox__dropdown {
  display: flex;
  align-items: center;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  );
}
.is-open .c-menu-toolbox__dropdown .u-icon {
  transform: scale(-1);
}

/*
** Component - Overlay
** ----------------------------------------------------------------------------- */
.c-overlay {
  position: fixed;
  inset: 0;
  z-index: var(--chipmunk--layer--overlay);
  display: flex;
  background-color: var(--chipmunk--color--background);
  opacity: 0;
  transform: translate3d(0, 2rem, 0);
  pointer-events: none;
  backface-visibility: hidden;
  transition: opacity, transform;
  transition-duration: var(--chipmunk--transition-duration);
}
.has-nav-open .c-overlay {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  pointer-events: all;
}
.c-overlay .container {
  height: 100%;
}

.c-overlay__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100% - var(--header-height, 0px));
  margin-top: var(--header-height, 0);
  padding-top: clamp(
    calc(var(--chipmunk--spacer) * 2.5),
    5 * 1vw,
    calc(var(--chipmunk--spacer) * 5)
  );
  padding-bottom: clamp(
    calc(var(--chipmunk--spacer) * 2.5),
    5 * 1vw,
    calc(var(--chipmunk--spacer) * 5)
  );
}

.c-overlay__menu {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.c-overlay__bottom:not(:first-child) {
  margin-top: 5vh;
}

/*
** Component - Page foot
** ----------------------------------------------------------------------------- */
.c-page-foot a:hover,
.c-page-foot button:hover {
  color: var(--chipmunk--color--black);
}

.c-page-foot__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--chipmunk--layout--column-gutter);
}
@media (min-width: 680px) {
  .c-page-foot__inner {
    flex-direction: row;
    text-align: left;
  }
}

.c-page-foot__column {
  display: flex;
  flex-direction: column;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.5),
    1 * 1vw,
    calc(var(--chipmunk--spacer) * 1)
  );
  width: 100%;
}
@media (min-width: 680px) {
  .c-page-foot__column:first-child {
    width: 50%;
    margin-right: auto;
  }
}
@media (min-width: 680px) {
  .c-page-foot__column:not(:first-child) {
    width: 33.3333333333%;
  }
}
@media (min-width: 1024px) {
  .c-page-foot__column:not(:first-child) {
    width: 16.6666666667%;
  }
}

.c-page-foot__description {
  color: var(--chipmunk--color--gray);
}

.c-page-foot__copy,
.c-page-foot__credits {
  color: var(--chipmunk--color--gray);
  text-align: center;
}
@media (min-width: 680px) {
  .c-page-foot__copy,
.c-page-foot__credits {
    text-align: left;
  }
}

.c-page-foot__credits {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: calc(var(--chipmunk--spacer) * 0.5);
  white-space: nowrap;
}
.c-page-foot__credits svg {
  width: auto;
  height: calc(var(--chipmunk--typography--line-height) * 1 * 1em);
  fill: currentColor;
}

/*
** Component - Page head
** ----------------------------------------------------------------------------- */
.c-page-head {
  z-index: var(--chipmunk--layer--head);
  background-color: var(--chipmunk--color--section);
}
.c-page-head.is-sticky {
  inset: 0 0 auto;
}
.admin-bar .c-page-head.is-sticky {
  top: 4.6rem;
}
@media (min-width: 783px) {
  .admin-bar .c-page-head.is-sticky {
    top: 3.2rem;
  }
}

.c-page-head__inner {
  padding-top: clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
  padding-bottom: clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
