  transition-property: padding;
  transition-duration: var(--chipmunk--transition-duration);
}
.is-condensed .is-sticky .c-page-head__inner {
  padding-top: clamp(
    calc(var(--chipmunk--spacer) * 0.5),
    1 * 1vw,
    calc(var(--chipmunk--spacer) * 1)
  );
  padding-bottom: clamp(
    calc(var(--chipmunk--spacer) * 0.5),
    1 * 1vw,
    calc(var(--chipmunk--spacer) * 1)
  );
}

.c-page-head__logo {
  color: var(--chipmunk--color--primary);
  font-weight: bold;
}
.c-page-head__logo a {
  display: block;
}
.c-page-head__logo a:hover {
  color: var(--chipmunk--color--black);
}
.c-page-head__logo img {
  display: inline-block;
  width: auto;
  height: var(--chipmunk--logo-height);
  vertical-align: middle;
}

.c-page-head__cta {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: calc(var(--chipmunk--spacer) * 0.75);
  margin-left: auto;
}

.c-page-head__search {
  position: absolute;
  inset: -100% 0 100%;
  z-index: var(--chipmunk--layer--base);
  display: flex;
  align-items: center;
  gap: calc(var(--chipmunk--spacer) * 1);
  background-color: var(--chipmunk--color--section);
  opacity: 0;
  pointer-events: none;
  transition-property: bottom, top, opacity;
  transition-duration: calc(var(--chipmunk--transition-duration) * 1.5);
}
.has-search-open .c-page-head__search {
  top: 0;
  bottom: 0;
  opacity: 1;
  pointer-events: all;
}
.c-page-head__search .c-form__error {
  display: none;
  position: absolute;
  top: 50%;
  right: 3em;
  max-width: 50%;
  font-size: 1.2rem;
  line-height: 1.25;
  text-align: right;
  pointer-events: none;
  transform: translateY(-50%);
}
@media (min-width: 680px) {
  .c-page-head__search .c-form__error {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.c-page-head__placeholder {
  height: var(--header-height, 0);
}

/*
** Component - Pagination
** ----------------------------------------------------------------------------- */
.c-pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  ) clamp(
    calc(var(--chipmunk--spacer) * 0.5),
    1 * 1vw,
    calc(var(--chipmunk--spacer) * 1)
  );
}

.c-pagination__title {
  color: var(--chipmunk--color--gray);
}

.c-pagination__item:last-child {
  text-align: right;
}
.c-pagination__item a,
.c-pagination__item span {
  display: inline-flex;
  align-items: center;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.25),
    0.5 * 1vw,
    calc(var(--chipmunk--spacer) * 0.5)
  );
}
.c-pagination__item a[href]:hover,
.c-pagination__item span[href]:hover {
  color: var(--chipmunk--color--primary);
}
.c-pagination__item .u-icon {
  font-size: 1.25em;
}
.c-pagination__item--disabled {
  visibility: hidden;
}

.c-pagination__button {
  margin-left: auto;
  margin-right: auto;
}

/*
** Component - Popup
** ----------------------------------------------------------------------------- */
.c-popup {
  position: fixed;
  inset: 0;
  z-index: var(--chipmunk--layer--popup);
  overflow: hidden;
  pointer-events: none;
  transform: translateX(-300%);
  transition: transform 0s var(--chipmunk--transition-duration);
}
.has-popup-open .c-popup {
  transform: translateX(0);
  pointer-events: all;
  transition: none;
}

.c-popup__close {
  position: absolute;
  top: clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
  right: clamp(
    calc(var(--chipmunk--spacer) * 1),
    2 * 1vw,
    calc(var(--chipmunk--spacer) * 2)
  );
  z-index: var(--chipmunk--layer--utils);
  color: var(--chipmunk--color--black);
  line-height: 1;
  transition-property: opacity, color;
  transition-duration: calc(var(--chipmunk--transition-duration) * 1.5);
}
.c-popup__close:hover {
  color: var(--chipmunk--color--primary);
}

.c-popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% - var(--chipmunk--layout--container-gutter) * 2);
  max-height: calc(100% - var(--chipmunk--layout--column-gutter) * 2);
  max-width: min(var(--chipmunk--layout--popup-width), 100vw - var(--scrollbar-width, 0px) - var(--chipmunk--layout--container-gutter) * 2);
  margin-top: 5%;
  padding: clamp(
    calc(var(--chipmunk--spacer) * 2),
    4 * 1vw,
    calc(var(--chipmunk--spacer) * 4)
  );
  background-color: var(--chipmunk--color--section);
  color: var(--chipmunk--color--black);
  opacity: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity calc(var(--chipmunk--transition-duration) * 1.5), margin calc(var(--chipmunk--transition-duration) * 1.5);
}
.has-popup-open .c-popup__content {
  opacity: 1;
  margin-top: 0;
}
.c-popup__content > img {
  width: auto;
  height: auto;
  max-height: 100%;
  margin: auto;
}

/*
** Component - Profile
** ----------------------------------------------------------------------------- */
.c-profile {
  display: flex;
  align-items: flex-start;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.75),
    1.5 * 1vw,
    calc(var(--chipmunk--spacer) * 1.5)
  );
}

.c-profile__avatar {
  max-width: 10rem;
}
@media (min-width: 680px) {
  .c-profile__avatar {
    max-width: 12.8rem;
  }
}
.c-profile__avatar img {
  height: auto;
}

.c-profile__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 0.75),
    1.5 * 1vw,
    calc(var(--chipmunk--spacer) * 1.5)
  );
}

.c-profile__meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: clamp(
    calc(var(--chipmunk--spacer) * 1.25),
    2.5 * 1vw,
    calc(var(--chipmunk--spacer) * 2.5)
  );
}

.c-profile__stats {
  color: var(--chipmunk--color--gray);
}

/*
** Component - Ratings
** ----------------------------------------------------------------------------- */
.c-ratings {
  display: flex;
  align-items: center;
  gap: calc(var(--chipmunk--typography--line-height) * 0.5 * 1em) calc(var(--chipmunk--typography--line-height) * 0.5 * 1em);
}

.c-ratings__form {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 0.125em;
}
.c-ratings__form > input {
  position: absolute;
  left: -9999em;
  opacity: 0;
}

.c-ratings__button {
  color: var(--chipmunk--color--gray-light);
