/*
** Blocks / Embeds
** ----------------------------------------------------------------------------- */

.wp-block-image,
.wp-block-embed {
  figcaption {
    @extend %caption;
  }

  &.alignwide,
  &.alignfull {
    figcaption {
      @extend %caption-indented;
    }
  }

  &.alignwide {
    figcaption {
      @include bp(lg) {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
}

.wp-block-video,
.wp-block-embed {
  position: relative;
  aspect-ratio: 16 / 9;

  .wp-block-embed__wrapper {
    display: contents;
  }

  video,
  iframe {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
  }
}
